.footer {
    text-align: center;
    background-color: #1b1b1b;
    color: rgb(161, 161, 161);
    padding: 2em;
}

.social_list {
    display: flex;
    justify-content: center;
    list-style: none;
    padding: 0 1em;
}

.social_list li {
    margin: 0 1em;
}

.social_list a {
    color: rgb(161, 161, 161);
    text-decoration: none;
}

.social_list a:hover {

    color: rgb(255, 255, 255);
}

.social_list svg {
    text-decoration: none;
    font-size: 1.5em;
    cursor: pointer;
    text-decoration: none;
}

.copy_right span {
    font-weight: bold;
    color: rgb(249, 249, 249);

}