.home {

    background-color: #1b1b1b;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    padding: 4em;
}


.home h1 {
    font-size: 2em;
    color:rgb(229, 227, 227);
}

.home p {
    margin-bottom: 2.5em;
    color:rgb(229, 227, 227);
}

.home h1 span {
    color: rgb(58, 58, 58);
    background-color:rgb(229, 227, 227);
    padding: 0 .2em;
    font-style: italic;
}

.home img {
    width: 350px;
    margin: 2em 0;
}