.btn {
    background-color: rgb(157, 139, 0);
    background-image: linear-gradient(to top left, rgb(198, 176, 4), rgb(224, 205, 0), rgb(255, 236, 69));
    color: #1b1b1b;
    padding: .8em 3em;
    text-decoration: none;
    border-radius: 5em;
    border: none;
    font-weight: bold;
    transition: .5s;
}

.btn:hover {
    background-color: rgb(206, 182, 2);
    background-image: linear-gradient(to top left, rgb(255, 236, 69), rgb(224, 205, 0), rgb(198, 176, 4));
    color: #1b1b1b;

}