.navbar {

    display: flex;
    justify-content: space-between;
    background-color: #1b1b1b;
    padding: 1em;
}


.list {

    display: flex;
    align-items: center;
    list-style: none;
    margin: 0 4em;

}

.item {

    margin-right: 2em;

}

.item a {

    color: rgb(161, 161, 161);
    text-decoration: none;
    transition: .2s;

}

.item a:hover {
    border-bottom: 2px solid rgb(161, 161, 161);
    font-weight: bold;
}

.logo {

    display: flex;
    align-items: flex-start;
    margin: 0 4em;
    width: 100px;



}