.curriculum {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    padding: 4em;
}

.curriculum h1 {
    margin-bottom: 1.5em;
}

.curriculum h3 {
    margin-top: 3em;
    color: rgb(229, 227, 227));
}

.curriculum h5 {
    color: rgb(229, 227, 227);
}

.curriculum p span {
    font-weight: bold;
    color: rgb(229, 227, 227);

}

.space {
    margin-top: 50px;
    border: 0.5px solid rgb(229, 227, 227);

}