.form {
    width: 350px;
    display: flex;
    flex-direction: column;
    justify-content: right;
    margin: auto;

}

.form h1 {
    text-align: center;
}

.form p {
    text-align: justify;
    padding-bottom: 1em;
}

.form label {
    font-weight: bold;
}

.form input {
    width: 100%;
    padding: .7em;
    border-radius: 0;
    border: none;
    background-color: #eeeeee;
    margin-bottom: 1.5em;


}

.form input::placeholder {
    color: #7b7b7b;

}